

import 'bootstrap/dist/css/bootstrap.min.css';


const Input = ({handler}) => {


        return (
            <div className="input-group">
            <input className="text-center form-control" type="text" onChange={handler} placeholder="Sabanci Username" aria-label="Username" aria-describedby="basic-addon2"/>
            </div>
        )
      
    }

export default Input


