import 'bootstrap/dist/css/bootstrap.min.css';
import React, { useState } from 'react'
import LessonList from "./LessonList.js"
import AlertHandler from "./AlertHandler"
import {Button,Form} from "react-bootstrap"
import axios from 'axios'
import Input from "./Input.js"


const NotifyForm = () => {

    const [selectedLessons,setSelectedLessons] = useState([])
    const [username,setUsername] = useState("")
    const [resultMessage,setResultMessage] = useState("")
    const [isSuccess,setIsSuccess] = useState(Boolean)


    const handleSubmit = async (event) => {
      event.preventDefault()


      try {

      const {successful,content} = (await axios.post("/",{lectures:selectedLessons,username:username})).data
            
      if (content) {
        setResultMessage(content)
        setIsSuccess(successful)
      }

      }
      catch (error) {
        console.log(error)
        setIsSuccess(false)
        setResultMessage(error.message)
      }


    }

    const handleChange = (event) =>  {
      setUsername(event.target.value)
    }



   return (
     <Form className="w-50">
        <Form.Group className="mb-3 d-flex flex-column">
        <Form.Label className="text-center">Select Courses</Form.Label>
        <LessonList setSelectedLessons={setSelectedLessons}/>
        <Form.Text className="text-muted text-center">
        You can select multiple Courses
      </Form.Text>
        </Form.Group>

        <Form.Group className="mb-0 d-flex flex-column">
        <Input handler={handleChange}/>
        <Button className="pt-100" onClick={handleSubmit}>Notify me</Button>
        </Form.Group>

        <Form.Group className="mt-3 d-flex flex-column">
        <AlertHandler content={resultMessage} isSuccess={isSuccess} />
        </Form.Group>

      </Form>
    )  

 /*  return (
    <Form className="align-items-center">
  <Form.Group className="mb-5" controlId="formBasicEmail">
    <Form.Label className="text-center">Select Course</Form.Label>
    <LessonList setSelectedLessons={setSelectedLessons}/>
    <Form.Text className="text-muted">
      You can select multiple Courses
    </Form.Text>
  </Form.Group>

  <Form.Group className="mb-5" controlId="formBasicEmail">
    <Form.Label className="text-center">Sabanci Username</Form.Label>
    <Input handler={handleChange}/>
    <Form.Text className="text-muted">
      Without mail part
    </Form.Text>
  </Form.Group>

  <Button variant="primary" type="submit">
    Submit
  </Button>
</Form>
  ) */

  


}
          

 
  


export default NotifyForm