import React from 'react'
import Select,{createFilter} from 'react-select'
import dersler from "./dersler.json"
import CustomOption from './CustomOption'

const lessons = Object.entries(dersler).map(([k,l]) => ({label:l,value:k}))




const LessonList = ({setSelectedLessons}) => {


    const handleChange  = (event) => setSelectedLessons(Object.entries(event).map((data) => data[1].value))



    return (
     <Select  
     filterOption={createFilter({ ignoreAccents: false })}
     isMulti 
     options={lessons} 
     components={{Option:CustomOption}}
     classNamePrefix="custom-select"
     onChange={handleChange}
      />
    )
}
          

 
  


export default LessonList